import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import TokenInfo from './tokenInfo';
import TopTrade from './component/top_trade';
import List from './component/list';
import styled from 'styled-components';


function App() {
  /* State 
  connected :  
  wsData : ticker   data :   
  tokens :    
  status	     (https://apidocs.bithumb.com/reference/%ED%98%84%EC%9E%AC%EA%B0%80-%EC%A0%95%EB%B3%B4-%EC%A1%B0%ED%9A%8C-all)
  */
  const [connected, setConnected] = useState(false);
  const [wsData, setWsData] = useState({});
  const [chartData, setChartData] = useState({});
  const [tokens, setTokens] = useState({});

  const webSocketUrl = 'wss://pubwss.bithumb.com/pub/ws';
  const apiGetUrl = 'https://api.bithumb.com/public/ticker/ALL_KRW';
  let ws = useRef(null);
  const symbols = Object.keys(TokenInfo).map((el) => `${el}_KRW`);
  const ws_request = {
    "type": "ticker",
    "symbols": symbols,
    "tickTypes": ["30M"]
  };

  //     
  useEffect(() => {
    const chart_ticker = ['BTC', 'ETH', 'BNB', 'XRP', 'ADA'];
    const getBasicInfo = async () => {
      try {
        const res = await axios.get(apiGetUrl);
        setTokens(res.data.data);
      } catch (e) {
        console.log('something went wrong during get basicinfo :( ', e);
      }
    };
    const getChartInfo = async (ticker) => {
      try {
        const url = `https://api.bithumb.com/public/candlestick/${ticker}_KRW/10m`
        const res = await axios.get(url);

        let data = new Object();
        data[ticker] = res.data;
        setChartData(Object.assign(chartData, data));
      } catch (e) {
        console.log('something went wrong during get chartinfo :( ', e);
      }
    }

    if (!ws.current) {
      ws.current = new WebSocket(webSocketUrl);
      ws.current.onopen = () => {
        console.log("connected to" + webSocketUrl);
        setConnected(true);
      };
      ws.current.onclose = (error) => {
        console.log("disconnect from" + webSocketUrl);
        console.log(error);
      };
      ws.current.onerror = (error) => {
        console.log("connection error" + webSocketUrl);
        console.log(error);
      };
      ws.current.onmessage = (msg) => {
        const data = JSON.parse(msg.data);
        setWsData(data);
      }
    }

    getBasicInfo();
    chart_ticker.forEach((ticker) => getChartInfo(ticker))
  }, []);

  //  send
  useEffect(() => {
    if (connected) {
      ws.current.send(JSON.stringify(ws_request));
    }
  }, [connected]);

  //   token  
  useEffect(() => {
    const data = wsData.content;
    if (data) {
      const symbol = data.symbol.split('_')[0];
      let tempObj = tokens;
      tempObj[symbol].closing_price = data.closePrice;
      tempObj[symbol].chg_Amt = data.chgAmt;
      tempObj[symbol].chg_Rate = data.chgRate;
      setTokens(tempObj);
    }
  }, [wsData]);

  return (
    <Body>
      <Header>
        <Logo src='https://i.ibb.co/khdTC5K/Githumb.png' alt='logo' />
      </Header>
      <Contents>
        <TopTrade tokens={tokens} chartData={chartData} />
        <List tokens={tokens} />
      </Contents>
    </Body>
  );
}

export default App;

//styled-components
const Body = styled.div`
font-family: 'Spoqa Han Sans Neo',Roboto,"Noto Sans KR",Arial,"Malgun Gothic",sans-serif;
display:flex;
flex-direction:column;
align-items: center;
`;
const Header = styled.div`
position: fixed;
top: 0;
left: 0;
right: 0;
height: 100px;
width: 100%;
border-bottom: 1px solid rgba(0,0,0,0.1);
background-color: #333333;
color: white;
text-align: center;
`;
const Logo = styled.img`
height: 100%;
-webkit-user-drag: none;
`;
const Contents = styled.div`
padding-top: 100px;
width: 1024px;
display: flex;
flex-direction: column;
`;